.popup__form__digital__disabled-edit{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 330px;
    min-height: 100px;
    overflow: scroll;
    padding: 16px 24px 40px 24px;
    background-color: var(--color-light-grey-button);
}
.popup__form__digital__active-edit{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 276px;
    min-height: 100px;
    overflow: scroll;
    padding: 16px 24px 40px 24px;
    background-color: var(--color-light-grey-button);
}
.popup__form__digital__active-edit::-webkit-scrollbar{
    display: none;
}
.popup__form__digital__disabled-edit::-webkit-scrollbar{
    display: none;
}