.not-found {
  display: flex;
  flex-direction: column;
  justify-content: end;
  align-items: center;
  gap: 12px;
  width: 1440px;
  max-height: 1024px;
  min-height: 800px;
  height: 100vh;
  background-image: url('../../images/not-found.png');
  background-position:bottom;
  box-sizing: border-box;
  padding: 80px;
}

.not-found_title {
  color: #171717;
}

.not-found_button {
  margin-top: 12px;
}