.main-sots {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  overflow: hidden;
  padding-top: 20px;
  display: grid;
  grid-template-columns: min-content min-content;
  grid-template-rows: 52px min-content min-content min-content min-content min-content;
  justify-content: center;
}