.popup__container_stocks {
  position: absolute;
  box-sizing: border-box;
  top: 0;
  left: -380px;
  min-height: 800px;
  padding: 40px;
  width: 1367px;
  display: flex;
  justify-content: space-between;
  background-color: var(--color-white);
  border-radius: 40px;
}
.stock__info {
  max-width: 607px;
  width: 100%;
  height: auto;
}

.popup__content {
  max-width: 520px;
}

.popup__instruction {
  padding: 0 0 16px 20px;
  margin: 0;
}
.stock__info__paragraph {
  padding-bottom: 16px;
  margin: 0;
}
.stock__info__paragraph:empty {
  padding-bottom: 0;
  list-style: none;
}

.stock__info__title {
  font-size: 20px;
font-style: normal;
font-weight: 600;
line-height: 130%;
margin: 8px 0 24px;
}