.popup__container__transport-edit{
    box-sizing: border-box;
    min-height: 800px;
    background-color: var(--color-white);
    position: relative;
    box-shadow: 4px 4px 8px 0px rgba(166, 166, 166, 0.15);
    margin: 112px auto;
    width: 600px;
    height: 800px;
    border-radius: 40px 0 0 40px;
    padding: 15px 24px 0 24px;
}

.popup__form__transport__disabled-edit{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 330px;
    min-height: 100px;
    overflow: scroll;
    padding: 16px 24px 40px 24px;
    background-color: var(--color-light-grey-button);
}

.popup__form__transport__active-edit{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 270px;
    min-height: 100px;
    overflow: scroll;
    padding: 16px 24px 40px 24px;
    background-color: var(--color-light-grey-button);
}

.popup__form__transport__active-edit::-webkit-scrollbar{
    display: none;
}

.popup__form__transport__disabled-edit::-webkit-scrollbar{
    display: none;
}

.checkbox__transport-edit{
    display: flex;
    align-items: center;
    height: 24px;
    line-height: 20px;
    position: relative;
    vertical-align: middle;
    font-size: 14px;
    -webkit-user-select: none;
    user-select: none;
    margin: 10px 0 10px 0;
}