.swing-button_demo {
  grid-column: 2/3;
  grid-row: 1/2;
}

.capital-list {
  grid-row: 2/3;
  grid-column: 1/3;
  display: grid;
  grid-template-columns: min-content min-content;
  grid-template-rows: auto;
}

.demo {
  grid-row: 2/3;
  grid-column: 1/3;
}

.sots {
  grid-column: 1/3;
  grid-row: 2/3;
  width: auto;
  height: 100%;
  display: grid;
  grid-template-columns: repeat(1, 168px 168px 250px 168px 168px);
  column-gap: 30px; 
  box-sizing: border-box;
}

.openpopup {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  background-color: #ff9e89;
  position: absolute;
  right: 40px;
  bottom: 40px;
  border: none;
  outline: none;
  width: 80px;
  height: 80px;
  border-radius: 50%;
}

.is {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100px;
  gap: 10px 0;
  height: 100px;
}

.precent img {
  width: 11.71px;
  align-self: flex-end;
}

.precent {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0 5px;
  color: var(--light-theme-green, #75b69b);
  text-align: center;

  font-family: Manrope;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 130%; 
}
.span-cost {
  color: var(--color-text-head, #1a4b5f);
  text-align: center;
  font-family: Manrope;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 130%; 
}
.span-cost span {
  margin-left: 4px;
}
.sot_wrapper {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 24px 0;
}

.sot_wrapper:nth-child(3) {
  margin-top: 100px;
  display: flex;
  align-items: center;
}

.centre span {
  z-index: 4;
}

.portfaill {
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--light-theme-active, var(--color-text-head, #1a4b5f));
  text-align: center;
  font-family: Manrope;
  font-size: 20px;
  font-style: normal;
  font-weight: 600;
  line-height: 130%; 
  width: 130%;
}

.portfaill span {
  margin-left: 4px;
}

.centre {
  width: 160px;
  height: 243px;
  gap: 16.49px 0;
  box-sizing: border-box;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(
    180deg,
    rgba(158, 206, 226, 1) 0%,
    rgba(50, 126, 157, 1) 100%
  );
  border-radius: 30px;
  box-shadow: 4px 8px 16px rgba(28, 88, 113, 0.3);
  flex-direction: column;
}

.cost-span {
  padding: 5.5px 8.25px;
  background-color: white;
  border-radius: 27.5px;
  color: var(--color-modal, #173c4b);
  text-align: center;
  font-family: Manrope;
  font-size: 28px;
  font-style: normal;
  font-weight: 700;
  line-height: 130%; 
}

.percent-span {
  color: var(--service-succes-for-dark, #6ae332);
  text-align: center;
  font-family: Manrope;
  font-size: 20px;
  font-style: normal;
  font-weight: 600;
  line-height: 130%; 
  display: flex;
  align-items: center;
  gap: 0 4px;
}

.percent-span img {
  width: 16px;
}

.cardd {
  position: relative;
  width: 164px;
  height: 148px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.sotaa:before,
.sotaa:after {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  transition: 0.5s;
  transform-origin: center;
  border: inherit;
  background-color: transparent;
}

.sotaa:before {
  transform: rotateZ(60deg);
  border-radius: 22px 22px 22px 22px;
}

.sotaa:after {
  transform: rotateZ(-60deg);
  border-radius: 23px 23px 23px 23px;
}

.sotaa {
  box-sizing: border-box;
  position: relative;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  width: 62%;
  height: 100%;
  border-radius: 23px 23px 23px 23px;
  border-top: 2px solid var(--border-color);
  border-bottom: 2px solid var(--border-color);
}

.sotaa:hover {
  cursor: pointer;
}

.centre:after {
  transform: rotateZ(-60deg);
  border-radius: 30px 30px 30px 30px;
  background: linear-gradient(
    240deg,
    rgba(158, 206, 226, 1) 0%,
    rgba(50, 126, 157, 1) 100%
  );
}

.centre:before {
  transform: rotateZ(61deg);
  border-radius: 30px 30px 30px 30px;
  background: linear-gradient(
    120deg,
    rgba(158, 206, 226, 1) 0%,
    rgba(50, 126, 157, 1) 100%
  );
}

.centre:before,
.centre:after {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  transition: 0.5s;
  transform-origin: center;
  border: inherit;
  z-index: 1;
}
